:root {
  --white: #ffffff;
  --btn-bg: #11469c;
}
.service_container {
  background: linear-gradient(
      170deg,
      #000000,
      #000000ab,
      #0000007e,
      #0f514a38,
      #06091ec1,
      #03040ae2,
      #010205
    ),
    url(../../assets/background/bg.png) center no-repeat;
  background-size: cover;
  .content_ {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    .name_ {
      color: var(--white);
      text-transform: uppercase;
      font-size: 20px;
    }
    .title_ {
      text-align: center;
      font-size: 35px;
      color: var(--white);
      margin-top: 10px;
    }
    .moon {
      margin-top: 30px;
      margin-bottom: 30px;
      .moon_kub {
        position: relative;
        display: flex;
        justify-content: center;
        .moon_img {
          opacity: 0.8;
          width: 650px;
        }
      }
      .box {
        position: absolute;
        display: flex;
        .text_ {
          display: flex;
          flex-direction: column;
          width: 350px;
          .titl {
            color: var(--white);
            font-size: 17px;
          }
          .bady {
            margin-top: 6px;
            color: #d1eaee;
            font-size: 12.5px;
          }
        }
        .end_ {
          text-align: end;
        }
        .start_ {
          text-align: start;
        }
        .k_left {
          margin: 5px 0 0 20px;
        }
        .k_right {
          margin: 5px 20px 0 0;
        }
        .kub {
          width: 80px;
          height: 80px;
          border-radius: 20px;
          display: flex;
          align-items: center;
          justify-content: center;
          background: #181043;
          .bitcoin {
            max-width: 60%;
          }
        }
      }

      .box_1 {
        left: -160px;
        top: 0;
      }

      .box_2 {
        left: -220px;
        top: 140px;
      }
      .box_3 {
        left: -160px;
        bottom: 37px;
      }
      .box_11 {
        right: -160px;
        top: 0;
      }

      .box_22 {
        right: -220px;
        top: 140px;
      }
      .box_33 {
        right: -160px;
        bottom: 0;
      }
    }
  }
}

