:root {
  --white: #ffffff;
  --btn-bg: #11469c;
}
.contact_container {
  background: linear-gradient(
      180deg,
      #01020b,
      #01020b,
      #0000007a,
      #0203089e,
      #04060ea0,
      #04050fcf,
      #000000
    ),
    url(../../assets/background/bg.png), no-repeat;
  background-size: cover;
  .content_ {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    .name_ {
      color: var(--white);
      text-transform: uppercase;
      font-size: 20px;
      text-align: center;
    }
    .title_ {
      text-align: center;
      font-size: 42px;
      color: var(--white);
      margin-top: 10px;
    }
    .contain_ {
      width: 80%;
      display: flex;
      flex-direction: column;
      .form_ {
        margin: 50px 0 10px 0;
        display: flex;
        justify-content: space-between;
        .block_ {
          width: 45%;
          // background: #ffffff1f;
          display: flex;
          flex-direction: column;
          .input_ {
            outline: none;
            border: none;
            background: #11469c5c;
            color: var(--white);
            font-weight: 500;
            letter-spacing: 1px;
            border-radius: 2px;
            border: 1.5px solid #11469c5c;
            padding: 10px 15px;
            margin-bottom: 30px;
            &::placeholder {
              color: #fbfbfb54;
            }
          }
          .textarea_ {
            height: 120px;
            outline: none;
            border: none;
            background: #11469c5c;
            color: var(--white);
            font-weight: 500;
            letter-spacing: 1px;
            border-radius: 2px;
            border: 1px solid #11469c5c;
            padding: 10px 15px;
            margin-bottom: 30px;
            &::placeholder {
              color: #fbfbfb54;
            }
          }
        }
      }
      .context_ {
        .title_2 {
          font-size: 20px;
          color: var(--white);
        }
        .box_ {
          margin-top: 15px;
          display: flex;
          flex-direction: column;
          .title_3 {
            font-size: 18px;
            color: var(--white);
          }
          .job_time {
            font-size: 16px;
            color: var(--btn-bg);
            letter-spacing: 2px;
            width: max-content;
          }
          .phone_num {
            font-size: 16px;
            color: var(--btn-bg);
            letter-spacing: 2px;
            width: max-content;
          }
          .email_time {
            font-size: 16px;
            color: var(--btn-bg);
            letter-spacing: 2px;
            width: max-content;

            .br_ {
              display: none;
            }
          }
        }
      }
    }
  }
}

