:root {
  --white: #ffffff;
  --btn-bg: #11469c;
}
.dolorem_container {
  background: linear-gradient(
      180deg,
      #01020b,
      #01020b,
      #0000007a,
      #0203089e,
      #04060ea0,
      #04050fcf,
      #000000
    ),
    url(../../assets/background/bg.png), no-repeat;
  background-size: cover;
  .content_ {
    display: flex;
    justify-content: space-between;
    .block_ {
      width: 46%;
    }
    .btn_gr {
      display: none;
    }
    .b_card {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      .card_ {
        position: relative;
        &::before {
          content: "";
          position: absolute;
          top: 0;
          left: 14%;
          width: 70%;
          height: 5px;
          background: var(--btn-bg);
          border-radius: 0 0 10px 10px;
        }
        &:nth-child(1) {
          margin-bottom: 30px;
        }
        &:nth-child(2) {
          margin-bottom: 30px;
        }
        width: 45%;
        background: #100a33;
        display: flex;
        flex-direction: column;
        padding: 15px;
        border-radius: 15px;
        text-align: center;
        .number_ {
          color: var(--btn-bg);
          font-size: 25px;
        }
        .body_ {
          color: #d1eaee;
          margin-top: 10px;
          font-size: 14px;
        }
      }
    }
    .b_text {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      .name_ {
        color: var(--white);
        text-transform: uppercase;
        font-size: 20px;
      }
      .title_ {
        font-size: 35px;
        color: var(--white);
        margin-top: 10px;
      }
      .body_ {
        color: #d1eaee;
        margin-top: 6px;
        font-size: 13px;
      }
      .ul_ {
        padding-left: 1rem;
        margin: 20px 0 20px 0;
        li {
          margin-bottom: 10px;
          font-size: 16px;
          color: var(--btn-bg);

          .span {
            color: var(--white);
          }
        }
      }
    }
  }
}
